.selectButton {
  position: relative;

  padding: 8px;
  font-size: 16px;
  line-height: 13px;
  letter-spacing: 0.04em;

  color: var(--title-text-color);
  background-color: transparent;

  &::before {
    content: '';
    display: block;

    position: absolute;
    bottom: 1px;
    left: 0;

    width: 0;
    height: 1px;

    background-color: var(--btn-border-color);
    transition: all var(--transition-duration) var(--timing-function);
  }
//   &:focus::before,
  &:hover::before {
    width: 100%;
  }
  &:hover {
    text-shadow: var(--hover-text-shadow);
    color: var(--hover-active-color);
  }
}

.active {
  position: relative;

  text-shadow: var(--hover-text-shadow);
  color: var(--hover-active-color);
}

.message {
    font-size: 16px;
    margin: auto;
    text-align: center;
    @include mixins.mq(tablet) {
        font-size: 20px;
    }
    @include mixins.mq(desktop) {
        font-size: 26px;
    }
}